@media (max-width: 1199.98px) {
  .feedback__input {
    width: 30%; }
  .why__item {
    width: 48%;
    margin-right: 0 !important; } }

@media (max-width: 480.98px) {
  .header__wr {
    justify-content: center; }
  .header__btn {
    margin-bottom: 20px; }
  .timer__colon:nth-child(4) {
    display: none; }
  .timer__item {
    margin-bottom: 20px; }
  .stocks__item {
    margin-left: auto;
    margin-right: auto; }
  .feedback__input {
    width: 100%;
    margin-bottom: 20px; }
  .why__item {
    width: 100%; }
  .social {
    margin-top: 25px;
    margin-bottom: 25px; }
  .members__item {
    margin: 0; }
  .social {
    margin-right: 0;
    margin-left: 0;
    justify-content: center;
    width: 100%; } }

@media (max-width: 375.98px) {
  .timer__item {
    width: 100%; }
  .timer__ttl {
    font-size: 60px; }
  .timer__colon {
    display: none; }
  .timer__wrapper {
    justify-content: center; }
  .timer__num {
    margin: 0 auto; } }

@media (max-width: 320.98px) {
  .timer__ttl {
    font-size: 60px; } }
